<template>
  <div class="dashboard">
    <div class="sidebar">
      <h2><span class="accent">|</span> CRUD OPERATIONS</h2>
      <img src="data:image/jpeg;base64," alt="Profile" class="avatar" />
      <h3>Karthi Madesh</h3>
      <p class="role">Admin</p>
      <ul>
        <li @click="goToHome">🏠 Home</li>
        <li @click="goToCourses" class="active">📘 Course</li>
        <li @click="goToStudents">🎓 Students</li>
        <li @click="goToPayments">💰 Payment</li>
        <li @click="goToReports">📊 Report</li>
        <li @click="goToSettings">⚙️ Settings</li>
      </ul>
      <button class="logout" @click="logout">Logout</button>
    </div>

    <div class="main">
      <div class="header">
        <h1>Courses</h1>
        <button class="add-course-button" @click="addCourse">Add New Course</button>
      </div>

      <div class="course-list">
        <table>
          <thead>
            <tr>
              <th>Course Name</th>
              <th>Instructor</th>
              <th>Duration</th>
              <th>Price</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="course in courses" :key="course.id">
              <td>{{ course.name }}</td>
              <td>{{ course.instructor }}</td>
              <td>{{ course.duration }}</td>
              <td>INR {{ course.price.toLocaleString() }}</td>
              <td>
                <button @click="editCourse(course.id)" class="action-button edit-button">Edit</button>
                <button @click="deleteCourse(course.id)" class="action-button delete-button">Delete</button>
              </td>
            </tr>
            <tr v-if="courses.length === 0">
              <td colspan="5">No courses found.</td>
            </tr>
          </tbody>
        </table>
      </div>

      <div v-if="showCourseForm" class="modal">
        <div class="modal-content">
          <h2>{{ editingCourseId ? 'Edit Course' : 'Add New Course' }}</h2>
          <form @submit.prevent="saveCourse">
            <div class="form-group">
              <label for="courseName">Course Name:</label>
              <input type="text" id="courseName" v-model="currentCourse.name" required>
            </div>
            <div class="form-group">
              <label for="instructor">Instructor:</label>
              <input type="text" id="instructor" v-model="currentCourse.instructor" required>
            </div>
            <div class="form-group">
              <label for="duration">Duration:</label>
              <input type="text" id="duration" v-model="currentCourse.duration" required>
            </div>
            <div class="form-group">
              <label for="price">Price (INR):</label>
              <input type="number" id="price" v-model="currentCourse.price" required>
            </div>
            <div class="form-actions">
              <button type="submit" class="save-button">Save</button>
              <button type="button" class="cancel-button" @click="cancelCourseForm">Cancel</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CourseView',
  data() {
    return {
      courses: [], // Initialize as an empty array
      showCourseForm: false,
      editingCourseId: null,
      currentCourse: {
        id: null,
        name: '',
        instructor: '',
        duration: '',
        price: 0,
      },
    };
  },
  methods: {
    // Navigation methods (copied from DashboardView, ensure these are consistent across your sidebar views)
    logout() {
      this.$router.push('/');
    },
    goToHome() {
      this.$router.push('/dashboard');
    },
    goToStudents() {
      this.$router.push('/students');
    },
    goToPayments() {
      this.$router.push('/payments');
    },
    goToCourses() {
      this.$router.push('/courses');
    },
    
    goToReports() {
      this.$router.push('/reports');
    },
    goToSettings() {
      this.$router.push('/settings');
    },

    // CRUD Operations for Courses
    addCourse() {
      this.editingCourseId = null;
      this.currentCourse = { id: null, name: '', instructor: '', duration: '', price: 0 };
      this.showCourseForm = true;
    },
    editCourse(id) {
      this.editingCourseId = id;
      const courseToEdit = this.courses.find(course => course.id === id);
      this.currentCourse = { ...courseToEdit }; // Create a copy
      this.showCourseForm = true;
    },
    saveCourse() {
      if (this.editingCourseId) {
        // Update existing course
        const index = this.courses.findIndex(course => course.id === this.editingCourseId);
        if (index !== -1) {
          this.courses.splice(index, 1, { ...this.currentCourse });
        }
      } else {
        // Add new course
        const newId = this.courses.length > 0 ? Math.max(...this.courses.map(c => c.id)) + 1 : 1;
        this.courses.push({ ...this.currentCourse, id: newId });
      }
      this.saveCoursesToLocalStorage(); // Save after any change
      this.showCourseForm = false;
    },
    deleteCourse(id) {
      if (confirm('Are you sure you want to delete this course?')) {
        this.courses = this.courses.filter(course => course.id !== id);
        this.saveCoursesToLocalStorage(); // Save after deletion
      }
    },
    cancelCourseForm() {
      this.showCourseForm = false;
    },

    // Local Storage Helper Methods
    loadCoursesFromLocalStorage() {
      const storedCourses = localStorage.getItem('courses');
      if (storedCourses) {
        this.courses = JSON.parse(storedCourses);
      } else {
        // If no data in local storage, load some initial English data
        this.courses = [
          { id: 1, name: 'Web Development Fundamentals', instructor: 'Alice Wonderland', duration: '8 Weeks', price: 12000 },
          { id: 2, name: 'Advanced Data Science', instructor: 'Bob The Builder', duration: '12 Weeks', price: 18500 },
          { id: 3, name: 'Mobile App Design (Flutter)', instructor: 'Charlie Chaplin', duration: '10 Weeks', price: 15000 },
          { id: 4, name: 'Digital Marketing Strategies', instructor: 'Diana Prince', duration: '6 Weeks', price: 9000 },
          { id: 5, name: 'Cybersecurity Essentials', instructor: 'Ethan Hunt', duration: '14 Weeks', price: 22000 },
        ];
        this.saveCoursesToLocalStorage(); // Save initial data to local storage
      }
    },
    saveCoursesToLocalStorage() {
      localStorage.setItem('courses', JSON.stringify(this.courses));
    },
  },
  mounted() {
    this.loadCoursesFromLocalStorage(); // Load data when component is created
  },
};
</script>

<style scoped>
/* Your existing styles here, no changes needed for persistence logic */
