  color: orange;
  font-size: 14px;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;
  text-align: left;
  cursor: pointer;
}

li.active, li:hover {
  background-color: #facc15;
  color: white;
  border-radius: 5px;
}

