  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  margin-right: 5px;
}

.edit-button {
  background-color: #4CAF50; /* Green */
  color: white;
}

.delete-button {
  background-color: #f44336; /* Red */
  color: white;
}

.action-button:hover {
  opacity: 0.9;
}

/* Modal for Add/Edit Course */
.modal {
  position: fixed;
