<template>
  <div class="pie-container">
    <h3>Payments Distribution</h3>
    <Pie :data="pieData" :options="options" />
  </div>
</template>

<script>
import {
  Chart as ChartJS,
  Tooltip,
  Legend,
  Title,
  ArcElement,
} from 'chart.js'
import { Pie } from 'vue-chartjs'

ChartJS.register(Title, Tooltip, Legend, ArcElement)

export default {
  name: 'PaymentsChart',
  components: { Pie },
  props: {
    payments: Number,
  },
  computed: {
    pieData() {
      return {
        labels: ['Received', 'Remaining'],
        datasets: [
          {
            backgroundColor: ['#10b981', '#d1d5db'],
            data: [this.payments, 1000000 - this.payments],
          },
        ],
      };
    },
    options() {
      return {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: {
            position: 'bottom',
          },
        },
      };
    },
  },
};
</script>

<style scoped>
.pie-container {
  margin-top: 40px;
  width: 300px;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
</style>
