<template>
  <div class="container">
    <div class="sidebar">
      <h2><span class="accent">|</span> CRUD OPERATIONS</h2>
      <img
        :src="avatarImage"
        alt="Profile"
        class="avatar"
      />
      <h3>Karthi Madesh</h3>
      <p class="role">Admin</p>
      <ul>
        <li @click="goToDashboard">🏠 Home</li>
        <li @click="goToCourses">📘 Course</li>
        <li class="active" @click="goToStudents">🎓 Students</li>
        <li @click="goToPayments">💰 Payment</li>
        <li @click="goToReports">📊 Report</li>
        <li @click="goToSettings">⚙️ Settings</li>
      </ul>
      <button class="logout" @click="logout">Logout</button>
    </div>

    <div class="main">
      <div class="header">
        <h2>Students List</h2>
        <button class="add-new-student-button" @click="addStudent">Add New Student</button>
      </div>

      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Email</th>
            <th>Phone</th>
            <th>Enroll Number</th>
            <th>Date of Admission</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="student in students" :key="student.id">
            <td>
              <img :src="student.avatar || avatarImage" class="avatar-small" />
              <input v-if="student.editing" v-model="student.name"  class="nameSt"/>
              <span v-else>{{ student.name }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.email" />
              <span v-else>{{ student.email }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.phone" />
              <span v-else>{{ student.phone }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.enrollNumber" />
              <span v-else>{{ student.enrollNumber }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.date" type="date" />
              <span v-else>{{ student.date }}</span>
            </td>
            <td>
              <button v-if="student.editing" @click="saveStudent(student.id)" class="action-button save-button">💾</button>
              <button v-else @click="editStudent(student.id)" class="action-button edit-button">✏️</button>
              <button @click="deleteStudent(student.id)" class="action-button delete-button">🗑️</button>
            </td>
          </tr>
          <tr v-if="students.length === 0">
            <td colspan="6" style="text-align: center;">No students found.</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StudentsView', // Убедитесь, что имя компонента правильное
  data() {
    return {
      avatarImage: "data:image/jpeg;base64,",
      students: [], // Initialize as an empty array
    };
  },
  // Использование created() для инициализации аватаров (это не влияет на Local Storage)
  created() {
    // В этом компоненте не нужно инициализировать аватар, так как он будет загружаться из Local Storage,
    // или установлен по умолчанию, если Local Storage пуст.
    // Если вы хотите, чтобы все студенты имели этот аватар по умолчанию,
    // его следует присваивать при создании нового студента или при загрузке.
  },
  mounted() {
    this.loadStudentsFromLocalStorage(); // Загружаем данные при монтировании компонента
  },
  methods: {
    // Методы навигации (убедитесь, что они соответствуют вашим маршрутам)
    goToDashboard() {
      this.$router.push("/dashboard");
    },
    goToCourses() {
      this.$router.push("/courses");
    },
    goToPayments() {
      this.$router.push("/payments");
    },
    goToStudents() {
      this.$router.push("/students");
    },
    goToReports() { // Make sure this is present
      this.$router.push('/reports');
    },
    goToSettings() { // Make sure this is present
      this.$router.push('/settings');
    },
    logout() {
      this.$router.push("/");
    },

    // CRUD операции для студентов
    addStudent() {
      const newId = this.students.length > 0 ? Math.max(...this.students.map(s => s.id)) + 1 : 1;
      const newStudent = {
        id: newId, // Добавляем уникальный ID
        name: "New Student",
        email: "new.student@example.com",
        phone: "111-222-3333",
        enrollNumber: "ENR" + String(newId).padStart(3, '0'), // Форматируем номер зачисления
        date: new Date().toISOString().slice(0, 10), // Текущая дата в формате YYYY-MM-DD
        avatar: this.avatarImage, // Аватар по умолчанию
        editing: true // Сразу переводим в режим редактирования для удобства
      };
      this.students.push(newStudent);
      this.saveStudentsToLocalStorage(); // Сохраняем после добавления
    },
    editStudent(id) {
      const studentToEdit = this.students.find(student => student.id === id);
      if (studentToEdit) {
        studentToEdit.editing = true;
      }
    },
    saveStudent(id) {
      const studentToSave = this.students.find(student => student.id === id);
      if (studentToSave) {
        studentToSave.editing = false;
        this.saveStudentsToLocalStorage(); // Сохраняем после сохранения
      }
    },
    deleteStudent(id) {
      if (confirm("Are you sure you want to delete this student?")) {
        this.students = this.students.filter(student => student.id !== id);
        this.saveStudentsToLocalStorage(); // Сохраняем после удаления
      }
    },

    // Методы Local Storage
    loadStudentsFromLocalStorage() {
      const storedStudents = localStorage.getItem('students');
      if (storedStudents) {
        this.students = JSON.parse(storedStudents);
      } else {
        // Начальные данные, если Local Storage пуст
        this.students = [
          {
            id: 1,
            name: "Emily White",
            email: "emily.white@example.com",
            phone: "555-123-4567",
            enrollNumber: "ENR001",
            date: "2023-09-01",
            avatar: this.avatarImage,
            editing: false
          },
          {
            id: 2,
            name: "David Green",
            email: "david.green@example.com",
            phone: "555-987-6543",
            enrollNumber: "ENR002",
            date: "2023-09-05",
            avatar: this.avatarImage,
            editing: false
          },
          {
            id: 3,
            name: "Sarah Brown",
            email: "sarah.brown@example.com",
            phone: "555-111-2222",
            enrollNumber: "ENR003",
            date: "2023-09-10",
            avatar: this.avatarImage,
            editing: false
          }
        ];
        this.saveStudentsToLocalStorage(); // Сохраняем начальные данные
      }
    },
    saveStudentsToLocalStorage() {
      localStorage.setItem('students', JSON.stringify(this.students));
    },
  }
};
</script>

<style scoped>
/* Ваши существующие стили */
.container {
  display: flex;
}

.sidebar {
  background: #f5f5f5;
  width: 200px;
  padding: 20px;
  text-align: center;
  height: 100vh;
}

.accent {
  color: #facc15;
  margin-right: 5px;
}

.avatar {
  border-radius: 50%;
  width: 100px;
  margin: 20px 0 10px;
}

.avatar-small {
  width: 30px;
  height: 30px; /* Добавлено для лучшего отображения */
  object-fit: cover; /* Добавлено для лучшего отображения */
  border-radius: 50%;
  margin-right: 8px;
  vertical-align: middle;
}

.role {
  color: orange;
  font-size: 14px;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;
  text-align: left;
  cursor: pointer;
}

li.active,
li:hover {
  background-color: #facc15;
  color: white;
  border-radius: 5px;
}

.logout {
  margin-top: 20px;
  padding: 10px;
  width: 100%;
  background: none;
  border: 1px solid #ccc;
  cursor: pointer;
}

.main {
  flex: 1;
  padding: 20px;
  background: #fff;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

/* Стиль для кнопки "Add New Student" */
.add-new-student-button {
  background-color: #facc15;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.add-new-student-button:hover {
  background-color: #e0b012;
}


table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05); /* Более мягкая тень */
}

th,
td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #f0f0f0; /* Добавлено для разделителей строк */
}

th {
  background: #f8f8f8; /* Более светлый фон, как на странице курсов */
  color: #333; /* Более темный цвет текста */
  font-weight: bold;
}

tr:hover {
  background-color: #f5f5f5; /* Цвет при наведении */
}

td button {
  margin-right: 5px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px 12px; /* Добавлены отступы */
  border-radius: 4px; /* Закругленные углы */
  font-size: 14px; /* Размер шрифта */
}

/* Стили кнопок действий */
.action-button {
  background-color: #4CAF50; /* Green for edit */
  color: white;
}

.action-button.delete-button {
  background-color: #f44336; /* Red for delete */
}

.action-button.save-button {
  background-color: #007bff; /* Blue for save, или можете использовать желтый из темы */
}

.action-button:hover {
  opacity: 0.9;
}

/* Стили для input в режиме редактирования */
td input {
  width: calc(100% - 10px); /* Уменьшаем ширину, чтобы учесть padding */
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

td input.nameSt{
  width: calc(100% - 100px);
}
</style>