<template>
  <div class="chart-container">
    <Bar :data="chartData" :options="chartOptions" />
  </div>
</template>

<script>
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
} from 'chart.js'
import { Bar } from 'vue-chartjs'

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)

export default {
  name: 'StatsChart',
  components: { Bar },
  props: {
    students: Number,
    courses: Number,
    users: Number,
  },
  computed: {
    chartData() {
      return {
        labels: ['Students', 'Courses', 'Users'],
        datasets: [
          {
            label: 'Dashboard Stats',
            backgroundColor: ['#3b82f6', '#ec4899', '#fb923c'],
            borderRadius: 8,
            data: [this.students, this.courses, this.users],
          },
        ],
      };
    },
    chartOptions() {
      return {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          y: {
            beginAtZero: true,
          },
        },
        plugins: {
          legend: {
            display: false,
          },
        },
      };
    },
  },
};
</script>

<style scoped>
.chart-container {
  width: 90%;
  max-width: 600px;
  height: 300px;
  margin: 0 auto;
}
</style>
